import UomService from '@/services/commons/UomService'
import DisabledClassMixin from '@/mixins/commons/DisabledClassMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'

let CurrencyTableMixin = {
  mixins: [DisabledClassMixin, LoadingMixin, ListLayoutMixin],
  props: {
    defaultCurrencyTableFilter: {
      type: String,
      default: null
    },
    defaultCurrencyTableOrderBy: {
      type: String,
      default: null
    },
    disabled: {
      type: String,
      default: null
    }
  },
  data: function () {
    return {
      currencyTableData: [],
      currencyTableFilterTimer: null,
      currencyTableFilter: this.defaultCurrencyTableFilter,
      currencyTableOrderBy: this.defaultCurrencyTableOrderBy,
      currencyTableContainer: 'loading-container',
      currencyTableBusy: false,
      currencyTableTotalRows: 0,
      currencyTableCurrentPage: 1,
      currencyTablePerPage: 10
    }
  },
  computed: {
    currencyTableNoDataText: function () {
      return this.currencyTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    currencyTableTotalPages: function () {
      return Math.ceil(this.currencyTableTotalRows / this.currencyTablePerPage)
    },
    currencyTablePagingInfo: function () {
      return this.pagingInfo(this.currencyTableCurrentPage, this.currencyTablePerPage, this.currencyTableTotalRows)
    }
  },
  methods: {
    loadCurrencyTableData (loadingContainerId) {
      let self = this
      this.currencyTableBusy = true
      this.currencyTableContainer = loadingContainerId || this.currencyTableContainer
      this.startLoading(this.currencyTableContainer)
      UomService.uoms({
        pageIndex: this.currencyTableCurrentPage - 1,
        pageSize: this.currencyTablePerPage,
        filter: this.currencyTableFilter,
        orderByField: this.currencyTableOrderBy,
        uomTypeEnumId: 'UT_CURRENCY_MEASURE',
        disabled: this.disabled
      }).then(function (response) {
        self.stopLoading(self.currencyTableContainer)
        self.currencyTableBusy = false
        self.currencyTableTotalRows = response.data.totalRows
        self.currencyTableData = response.data.uomList || []
      }).catch(function (error) {
        self.stopLoading(self.currencyTableContainer)
        self.currencyTableBusy = false
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    refreshCurrencyTable () {
      this.loadCurrencyTableData()
    },
    filterCurrencyTable (filter) {
      let self = this
      if (this.currencyTableFilter !== filter) {
        clearTimeout(this.currencyTableFilterTimer)
        this.currencyTableFilterTimer = setTimeout(() => {
          self.currencyTableFilter = filter
          self.loadCurrencyTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.currencyTableFilterTimer)
      }
    },
    orderCurrencyTable (orderBy, active) {
      this.currencyTableOrderBy = (active ? '' : '-') + orderBy
      this.loadCurrencyTableData()
    }
  },
  watch: {
    currencyTableCurrentPage: function () {
      this.refreshCurrencyTable()
    },
    currencyTablePerPage: function () {
      this.refreshCurrencyTable()
    }
  }
}

export default CurrencyTableMixin
